import {
  ChangeEvent,
  ClipboardEvent,
  KeyboardEvent,
  forwardRef,
  FunctionComponent,
  PropsWithoutRef,
  Ref,
  useContext,
  useMemo,
  useState,
  useEffect,
  useRef
} from 'react';
import styled, { css } from 'styled-components';

import {
  defaultThemeProp,
  BaseProps,
  ForwardProps,
  Modal,
  ModalManagerContext,
  Tag,
  Button,
  ModalMethods,
  useI18n,
  Count,
  Flex,
  Text,
  CardHeader,
  Card,
  Actions,
  Icon,
  registerIcon,
  CardContent,
  Progress,
  EmptyState,
  ErrorState,
  Popover,
  Menu,
  MenuProps,
  useConsolidatedRef,
  CardFooter,
  Banner,
  ErrorStateProps
} from '@pega/cosmos-react-core';
import * as tagIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/tag.icon';
import { StyledTag } from '@pega/cosmos-react-core/lib/components/Badges/Tag';
import { StyledCardContent } from '@pega/cosmos-react-core/lib/components/Card/CardContent';
import MultiSelectInput from '@pega/cosmos-react-core/lib/components/ComboBox/MultiSelectInput/MultiSelectInput';
import { StyledCardFooter } from '@pega/cosmos-react-core/lib/components/Card/CardFooter';

registerIcon(tagIcon);

export interface SelectedTag {
  text: string;
  newTag?: boolean;
}

export interface TagsProps extends BaseProps {
  /** A list of tags. */
  tags: string[];
  /**
   * A list of available tags to add from.
   * @default []
   * */
  availableTags?: string[];
  /** If true, a loading indicator will render in place of the tags list. */
  loading?: boolean;
  /** If defined, an error message will render in the component and actions will be disabled. */
  error?: boolean | string | ErrorStateProps;
  /** Callback triggered when a search query is changed in the Edit tags modal. */
  onSearch?: (filterValue: string) => void;
  /** Callback triggered when a tag is clicked. */
  onTagClick?: (tag: string) => void;
  /** Callback triggered when modified tags are submitted from the modal. */
  onEditTags?: (tags: SelectedTag[]) => Promise<void>;
  /** Ref for the Tags card. */
  ref?: Ref<HTMLElement>;
}

interface EditTagModalProps {
  tags: TagsProps['tags'];
  availableTags: TagsProps['availableTags'];
  onSearch?: TagsProps['onSearch'];
  onEditTags: NonNullable<TagsProps['onEditTags']>;
}

export const StyledTags = styled.article<{ isLoading?: boolean; viewAll?: boolean }>(
  ({ isLoading, theme, viewAll }) => {
    return css`
      ${StyledCardContent} {
        max-height: ${viewAll ? 'fit-content' : '10rem'};
        overflow-y: hidden;
      }

      ${StyledCardFooter} {
        margin-block-start: ${theme.base.spacing};
      }

      ${StyledTag} {
        cursor: pointer;
      }

      ${isLoading &&
      css`
        & > ${StyledCardContent} {
          height: 4rem;
        }
      `}
    `;
  }
);

StyledTags.defaultProps = defaultThemeProp;

const StyledMenuPopover = styled.div`
  width: 40ch;
`;

const StyledBanner = styled(Banner)(({ theme }) => {
  return css`
    padding-block-end: calc(2 * ${theme.base.spacing});
  `;
});

StyledBanner.defaultProps = defaultThemeProp;

const EditTagModal = ({ tags, availableTags = [], onSearch, onEditTags }: EditTagModalProps) => {
  const { ModalContext } = useContext(ModalManagerContext);
  const { dismiss } = useContext(ModalContext);
  const inputRef = useRef<HTMLInputElement>(null);
  const [filterValue, setFilterValue] = useState('');
  const [editError, setEditError] = useState('');
  const [selected, setSelected] = useState<SelectedTag[]>([]);
  const [items, setItems] = useState<MenuProps['items']>([]);
  const t = useI18n();

  useEffect(() => {
    setSelected(
      tags.map(tag => {
        return { text: tag };
      })
    );
  }, [tags]);

  useEffect(() => {
    const menuItems = availableTags
      .filter(tag => !selected.some(x => x.text === tag))
      .map(tag => {
        return { id: tag, primary: tag };
      });

    if (
      filterValue &&
      !menuItems.some(x => x.id === filterValue) &&
      !selected.some(x => x.text === filterValue)
    ) {
      menuItems.push({
        id: `custom:${filterValue}`,
        primary: t('use_input_value', [filterValue])
      });
    }

    setItems(menuItems);
  }, [availableTags, filterValue, selected]);

  const setInputValue = (value: string) => {
    setEditError('');
    setFilterValue(value);
    onSearch?.(value);
  };

  const addTag = (tag: string) => {
    if (tag && !selected.find(x => x.text === tag)) {
      if (items.find(x => x.id === tag)) {
        setSelected(curr => [...curr, { text: tag }]);
      } else {
        setSelected(curr => [...curr, { text: tag, newTag: true }]);
      }
      setInputValue('');
    }
  };

  const handlePaste = (e: ClipboardEvent<HTMLInputElement>) => {
    const potentialTags = e.clipboardData.getData('Text').split(' ');

    potentialTags.forEach(potentialTag => {
      addTag(potentialTag);
    });
  };

  const handleKeyDown = (e: KeyboardEvent<HTMLInputElement>) => {
    if (e.key === ' ') {
      addTag(filterValue);
    }
  };

  return (
    <Modal
      heading={t('edit_tags')}
      actions={
        <>
          <Button onClick={dismiss}>{t('cancel')}</Button>
          <Button
            variant='primary'
            onClick={() => {
              onEditTags(selected)
                .then(dismiss)
                .catch((error: unknown) => {
                  if (error instanceof Error) {
                    setEditError(error.message || t('unknown_error'));
                  } else {
                    setEditError(t('unknown_error'));
                  }
                });
            }}
          >
            {t('submit')}
          </Button>
        </>
      }
    >
      {editError && (
        <StyledBanner
          id='edit-tags-error'
          variant='urgent'
          messages={[editError]}
          onDismiss={() => {
            setEditError('');
          }}
        />
      )}
      <MultiSelectInput
        ref={inputRef}
        selected={selected.map(({ text }) => {
          return { id: text, text };
        })}
        value={filterValue}
        onChange={(e: ChangeEvent<HTMLInputElement>) => {
          setInputValue(e.target.value.trim());
        }}
        onRemove={(id: string, index: number) => {
          if (selected[index].text === id) {
            const updatedSelected = [...selected];
            updatedSelected.splice(index, 1);
            setSelected(updatedSelected);
            setEditError('');
          }
        }}
        onBlur={() => {
          setInputValue('');
        }}
        onPaste={handlePaste}
        onKeyDown={handleKeyDown}
        autoFocus
      />
      <Popover
        as={StyledMenuPopover}
        show={!!filterValue && !!items?.length}
        target={inputRef.current}
        placement='bottom-start'
        modifiers={[
          {
            name: 'flip',
            enabled: true,
            options: {
              fallbackPlacements: ['top-start']
            }
          },
          {
            name: 'preventOverflow',
            enabled: true
          },
          {
            name: 'hide',
            enabled: false
          }
        ]}
        onMouseDown={(e: MouseEvent) => e.preventDefault()}
      >
        <Menu
          role='listbox'
          mode='action'
          items={items}
          focusControlEl={inputRef.current || undefined}
          onItemClick={id => {
            if (id.includes('custom:')) {
              const tagValue = id.replace('custom:', '');
              if (!selected.some(x => x.text === tagValue)) {
                setSelected(curr => [...curr, { text: tagValue, newTag: true }]);
              }
            } else {
              setSelected(curr => [...curr, { text: id }]);
            }
            setInputValue('');
          }}
          arrowNavigationUnsupported
        />
      </Popover>
    </Modal>
  );
};

const Tags: FunctionComponent<TagsProps & ForwardProps> = forwardRef(
  (
    {
      tags,
      availableTags,
      loading,
      error,
      onSearch,
      onTagClick,
      onEditTags,
      ...restProps
    }: PropsWithoutRef<TagsProps>,
    ref: TagsProps['ref']
  ) => {
    const { create: createModal } = useContext(ModalManagerContext);
    const editModalMethods = useRef<ModalMethods<EditTagModalProps>>();
    const containerRef = useRef<HTMLDivElement>(null);
    const contentRef = useRef<HTMLDivElement>(null);
    const consolidatedRef = useConsolidatedRef(containerRef, ref);
    const [footer, setFooter] = useState(false);
    const [viewAll, setViewAll] = useState(false);
    const t = useI18n();

    useEffect(() => {
      if (containerRef.current && contentRef.current) {
        if (
          contentRef.current.getBoundingClientRect().bottom >
          containerRef.current.getBoundingClientRect().bottom
        ) {
          setFooter(true);
        } else {
          setFooter(false);
        }
      }
    }, [containerRef.current, contentRef.current, tags]);

    useEffect(() => {
      if (!onEditTags) {
        editModalMethods.current?.dismiss();
      } else {
        editModalMethods.current?.update({
          tags,
          availableTags,
          onSearch,
          onEditTags
        });
      }
    }, [availableTags, tags, onSearch, onEditTags]);

    const openEditModal = () => {
      if (!onEditTags) return;

      editModalMethods.current = createModal(EditTagModal, {
        tags,
        availableTags,
        onSearch,
        onEditTags
      });
    };

    const content = useMemo(() => {
      if (loading) {
        return <Progress placement='local' />;
      }

      if (error) {
        if (typeof error === 'boolean') return <ErrorState />;
        if (typeof error === 'string') return <ErrorState message={error} />;
        return <ErrorState {...error} />;
      }

      return tags.length > 0 ? (
        <Flex ref={contentRef} container={{ gap: 1, wrap: 'wrap' }}>
          {tags.map(tag => {
            return (
              <Tag key={tag} onClick={() => onTagClick?.(tag)}>
                {tag}
              </Tag>
            );
          })}
        </Flex>
      ) : (
        <EmptyState />
      );
    }, [loading, error, tags]);

    return (
      <Card
        ref={consolidatedRef}
        {...restProps}
        as={StyledTags}
        viewAll={viewAll}
        isLoading={loading}
      >
        <CardHeader>
          <Flex container={{ alignItems: 'center', gap: 1 }}>
            <Icon name='tag' />
            <Text variant='h2'>{t('tags')}</Text>
            <Count>{tags.length}</Count>
          </Flex>
          <Actions
            items={
              onEditTags
                ? [
                    {
                      id: 'editTags',
                      text: t('edit_tags'),
                      icon: 'pencil',
                      onClick: openEditModal,
                      disabled: !!error
                    }
                  ]
                : []
            }
          />
        </CardHeader>
        <CardContent>{content}</CardContent>
        {footer && (
          <CardFooter justify='center'>
            <Button variant='link' onClick={() => setViewAll(curr => !curr)}>
              {viewAll ? t('view_less') : t('view_all')}
            </Button>
          </CardFooter>
        )}
      </Card>
    );
  }
);

export default Tags;
