import {
  FunctionComponent,
  forwardRef,
  Ref,
  PropsWithoutRef,
  KeyboardEvent,
  useRef,
  useCallback
} from 'react';
import styled, { css } from 'styled-components';

import { ForwardProps } from '../../../types';
import { defaultThemeProp } from '../../../theme';
import Selectable, { StyledSelectable } from '../../Badges/Selection';
import { StyledFormControl } from '../../FormControl';
import Flex from '../../Flex';
import { useArrows, useConsolidatedRef, useDirection } from '../../../hooks';
import { cap } from '../../../utils';
import { StyledButton } from '../../Button';

import { MultiSelectInputProps } from './MultiSelectInput.types';

export const StyledMultiSelect = styled.div<MultiSelectInputProps>(props => {
  const {
    theme: {
      components: {
        'form-control': formControl,
        input: { height }
      },
      base
    }
  } = props;

  return css`
    min-height: ${height};

    @media (pointer: coarse) {
      min-height: ${base['hit-area']['finger-min']};
    }

    &:focus-within {
      border-color: ${formControl[':focus']['border-color']};
      box-shadow: ${formControl[':focus']['box-shadow']};
    }

    &:hover:not([readonly]):not([disabled]):focus-within {
      border-color: transparent;
    }

    ul {
      list-style: none;
    }

    input {
      min-width: 0;
      width: 100%;
      color: ${formControl['foreground-color']};
      text-overflow: ellipsis;
      margin-inline-start: ${base.spacing};
    }

    input,
    input:focus {
      border: none;
      outline: none;
      flex-grow: 1;
      background-color: transparent;

      &:read-only {
        cursor: default;
      }
    }

    ${StyledSelectable} {
      margin: calc(0.25 * ${base.spacing});
    }

    > ${StyledButton} {
      border-radius: calc(${base['border-radius']} * ${formControl['border-radius']});
      height: calc(${height} - 2 * ${formControl['border-width']});
    }
  `;
});

StyledMultiSelect.defaultProps = defaultThemeProp;

const MultiSelectInput: FunctionComponent<MultiSelectInputProps & ForwardProps> = forwardRef(
  (props: PropsWithoutRef<MultiSelectInputProps>, ref: Ref<HTMLInputElement>) => {
    const {
      selected = [],
      value,
      onRemove,
      onChange,
      onKeyDown,
      actions,
      readOnly,
      disabled,
      status,
      placeholder,
      autoFocus,
      ...restProps
    } = props;

    const inputRef = useConsolidatedRef<HTMLInputElement>(ref);
    const listRef = useRef<HTMLUListElement>(null);
    useArrows(listRef, { selector: 'div[role="button"], input', cycle: false, dir: 'left-right' });

    const onInputKeyDown = useCallback(
      (e: KeyboardEvent<HTMLInputElement>) => {
        const lastIndex = selected.length - 1;
        if (e.key === 'Backspace' && !value && selected.length > 0) {
          onRemove?.(selected[lastIndex].id, lastIndex);
        }
        onKeyDown?.(e);
      },
      [onKeyDown, value, selected]
    );

    const { end } = useDirection();

    return (
      <Flex
        as={StyledMultiSelect}
        forwardedAs={StyledFormControl}
        container={{ alignItems: 'center' }}
        {...{
          readOnly,
          disabled,
          status
        }}
      >
        <Flex
          ref={listRef}
          as='ul'
          item={{ grow: 1 }}
          container={{ alignItems: 'center', wrap: 'wrap' }}
          onBlur={() => {
            listRef.current?.querySelectorAll<HTMLElement>('div[role="button"]').forEach(el => {
              el.tabIndex = -1;
            });
            if (inputRef.current) inputRef.current.tabIndex = 0;
          }}
        >
          {selected.map((element, idx) => {
            return (
              <li key={element.id}>
                <Selectable
                  id={element.id.toString()}
                  disabled={readOnly || disabled}
                  status={status}
                  onRemove={id => {
                    onRemove?.(id, idx);
                    inputRef.current?.focus();
                  }}
                  onKeyDown={(e: KeyboardEvent<Element>) => {
                    // prevent browser's Back on FF
                    if (e.key === 'Backspace') e.preventDefault();
                    if (idx === selected.length - 1 && e.key === `Arrow${cap(end)}`)
                      inputRef.current?.focus();
                  }}
                >
                  {element.text}
                </Selectable>
              </li>
            );
          })}
          <Flex as='li' item={{ grow: 1 }} container>
            <input
              size={5}
              ref={inputRef}
              value={value}
              // no-op avoids react uncontrolled warning
              onChange={onChange || (() => {})}
              onKeyDown={onInputKeyDown}
              readOnly={readOnly || !onChange}
              placeholder={selected?.length ? undefined : placeholder}
              autoComplete='off'
              // eslint-disable-next-line jsx-a11y/no-autofocus
              autoFocus={autoFocus}
              {...restProps}
            />
          </Flex>
        </Flex>
        {actions}
      </Flex>
    );
  }
);

export default MultiSelectInput;
